<template>
  <div class="messages" v-if="message">
    <BasePanel>
      <template #panel-head>
        <router-link class="link-home" to="/">主页</router-link>
        /
        <span>新消息</span>
      </template>
      <template #panel-content>
        <div class="message-list" v-if="message.hasnot_read_messages.length">
          <div class="message-item" v-for="notRead in message.hasnot_read_messages" :key="notRead.id">
            <span class="author">{{notRead.author.loginname}}</span>
            <span class="type">{{notRead.type === 'reply' ? '回复了你的话题':'在话题'}}</span>
            <span class="title"><router-link :to="`/topic/${notRead.topic.id}`">{{notRead.topic.title}}</router-link></span>
            <span class="at" v-if="notRead.type === 'at'">中@了你</span>
          </div>
        </div>
        <div class="not-message" v-else>
          <span>无消息</span>
        </div>
      </template>
    </BasePanel>
    <BasePanel>
      <template #panel-head>
        <span>过往信息</span>
      </template>
      <template #panel-content>
        <div class="message-list" v-if="message.has_read_messages.length">
          <div class="message-item" v-for="read in message.has_read_messages" :key="read.id">
            <span class="author">{{read.author.loginname}}</span>
            <span class="type">{{read.type === 'reply' ? '回复了你的话题':'在话题'}}</span>
            <span class="title"><router-link :to="`/topic/${read.topic.id}`">{{read.topic.title}}</router-link></span>
            <span class="at" v-if="read.type === 'at'">中@了你</span>
          </div>
        </div>
      </template>
    </BasePanel>
  </div>
</template>

<script>
import BasePanel from '../components/layout/BasePanel.vue'
import {getMessage,markAllMessage} from '../plugins/api'
export default {
  components: { BasePanel },
  data(){
    return {
      message: null
    }
  },
  async created(){
    const accesstoken = sessionStorage.getItem('accesstoken')
    const res = await getMessage(accesstoken)
    console.log(res)
    this.message = res.data
    // 有未读消息的话，需要标记为已读
    if(res.data.hasnot_read_messages.length){
      await markAllMessage(accesstoken)
    }
  }
}
</script>

<style lang='less'>
.link-home{
  color: #80bd01;
}
.message-list{
  width: 1400px;
  margin: 0 auto;
  .message-item{
    border-bottom: 1px solid #ccc;
    padding: 10px 0;
    padding-left: 15px;
    .author,.title>a{
      color: #76d3ef;
    }
    .type,.at{
      margin-left: 10px;
      margin-right: 10px;
    }
  }
}
.not-message{
  height: 60px;
  line-height: 60px;
  span{
    margin-left: 15px;
  }
}
</style>